/**
 *  Status Panel
 */
.status-panel > div {
  display: inline-block;
  margin-left: 21px;
}
.status-panel .button-link {
  background-color: #FF5739;
  color: #FFF;
  text-decoration: none;
  text-transform: uppercase;
  line-height: 10px;
  font-weight: 600;
