/* Popover */
.popover {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9999;
  display: none;
  max-width: 279px;
  padding: 1px;
  text-align: center;
  white-space: normal;
  background-color: #fff;
  background-clip: padding-box;
  border-radius: 6px;
  -webkit-box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.22);
          box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.22);
}
.popover.top {
  margin-top: -10px;
}
.popover.right {
  margin-left: 10px;
}
.popover.bottom {
  margin-top: 10px;
}
.popover.left {
  margin-left: -10px;
}

.popover-title {
  padding: 30px 30px 0;
  margin: 0;
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.714;
  text-transform: uppercase;
  letter-spacing: -0.35px;
}

.popover-content {
  padding: 25px 30px 30px;
  color: #5D5D5D;
  font-family: Arial, sans-serif;
  font-size: 14px;
  line-height: 1.429;
}

.popover > .arrow {
  position: absolute;
  display: block;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
  border-width: 11px;
  margin-left: 0;
  overflow: visible;
}
.popover > .arrow:after {
  position: absolute;
  display: block;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
  content: none;
  z-index: 9999;
  background: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  position: absolute;
  left: auto;
  top: auto;
  width: auto;
  height: auto;
  -webkit-transform: none;
  transform: none;
  content: "";
  border-width: 10px;
}
.popover.top > .arrow {
  bottom: -11px;
  left: 50%;
  margin-left: -11px;
  border-bottom-width: 0;
}
.popover.top > .arrow:after {
  bottom: 1px;
  margin-left: -10px;
  content: " ";
  border-top-color: #fff;
  border-bottom-width: 0;
}
.popover.right > .arrow {
  top: 50%;
  left: -11px;
  margin-top: -11px;
  border-left-width: 0;
}
.popover.right > .arrow:after {
  bottom: -10px;
  left: 1px;
  content: " ";
  border-right-color: #fff;
  border-left-width: 0;
}
.popover.bottom > .arrow {
  top: -11px;
  left: 50%;
  margin-left: -11px;
  border-top-width: 0;
}
.popover.bottom > .arrow:after {
  top: 1px;
  margin-left: -10px;
  content: " ";
  border-top-width: 0;
  border-bottom-color: #fff;
}
.popover.left > .arrow {
  top: 50%;
  left: auto;
  right: -11px;
  margin-top: -11px;
  border-right-width: 0;
}
.popover.left > .arrow:after {
  left: auto;
  right: 1px;
  bottom: -10px;
  content: " ";
  border-right-width: 0;
  border-left-color: #fff;
}

