/* Modal */
.tinvwl-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity 0.3s ease, visibility 0.3s ease;
  transition: opacity 0.3s ease, visibility 0.3s ease;
  background: #191919;
}

.tinvwl-modal.tinvwl-modal-open .tinvwl-overlay {
  visibility: visible;
  opacity: 0.5;
}

