  position: fixed;
  outline: none !important;
  -webkit-backface-visibility: hidden;
  visibility: hidden;
  opacity: 0;
  text-align: left;
  -webkit-transition: opacity 0.3s ease, visibility 0.3s ease;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}
